import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
//import { Airline } from "../model/airline.component";

@Injectable({
  providedIn: 'root'
})

export class AirlineService{

  private baseUrl = 'http://35.88.136.178:8081/api/v1.0/flight/airline';

  host:string = "http://35.88.136.178:8081/api/v1.0/flight/airline/airlines";

  constructor(private httpClient:HttpClient){}

  
  addAirline(airline: Object): Observable<Object> {
    return this.httpClient.post(`${this.baseUrl}/register`,airline);
  }

  viewAllAirline() {
    return this.httpClient.get(this.host);
  }

  editAirline(airline: Object): Observable<Object> {
    return this.httpClient.put(`${this.baseUrl}/editAirline`,airline);
  }

}
