import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Flight } from '../model/flight.component';
import { ActivatedRoute, Router } from '@angular/router';
import { FlightService } from '../services/flight.service';
//import { Router } from '@angular/router';
import { SearchCriteria } from '../model/search-criteria.component';

@Component({
  selector: 'app-search-flight',
  templateUrl: './search-flight.component.html',
  styleUrls: ['./search-flight.component.css']
})
export class SearchFlightComponent implements OnInit {

  //flights:Observable<Flight[]>;
  flights:any[] = [];
  searchCriteria: SearchCriteria;

  constructor(private route: ActivatedRoute,private flightService: FlightService, private router: Router) {
    this.searchCriteria = router.getCurrentNavigation().extras.state.data;
   }

  ngOnInit(): void {
    
    console.log('got search criteria:',this.searchCriteria);
    this.searchFlight(this.searchCriteria);

  }

  searchFlight(searchCriteria){
    this.flightService.searchFlight(searchCriteria)
    .subscribe({
      next: (res:any)=>{
        console.log(res);
        this.flights = res;
      }
    })
  }

  
}
