import { Component, OnInit } from '@angular/core';
import { Booking } from '../model/booking.component';
import { ActivatedRoute, Router } from '@angular/router';
import { MyBookingService } from '../services/my-booking.service';

@Component({
  selector: 'app-booking-details-mail',
  templateUrl: './booking-details-mail.component.html',
  styleUrls: ['./booking-details-mail.component.css']
})
export class BookingDetailsMailComponent implements OnInit {

  booking: any[] = [];
  mail: String;

  constructor(private route: ActivatedRoute, private router: Router,
    private myBookingService: MyBookingService) { }

  ngOnInit() {
    //this.booking = new Booking();


    this.mail = this.route.snapshot.paramMap.get('mail');
    console.log('got mail:',this.mail);

    this.myBookingService.getBookingHistory(this.mail)
    .subscribe({
      next: (res:any)=>{
        console.log(res);
        this.booking = res;
        //console.log('so this is: ',res[0].mealOpt);
      }
    })
  }
}
