import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MyBookingService } from '../services/my-booking.service';
import { User } from '../model/user.component';
import { Booking } from '../model/booking.component';

@Component({
  selector: 'app-search-booking-pnr',
  templateUrl: './search-booking-pnr.component.html',
  styleUrls: ['./search-booking-pnr.component.css']
})
export class SearchBookingPnrComponent implements OnInit {

//   scheduleFlight:ScheduledFlight;
//   scheduleFlightId:number;
//   show:boolean=false;
  user: User=new User();
  booking: Booking = new Booking();
  pnr:String;

  constructor(private myBookingService: MyBookingService, private router: Router) { }

  ngOnInit(): void {
    //this.myBookingService=new MyBookingService();
  }

  getBookedDetails(pnr:String){
    this.myBookingService.getBookedDetails(pnr)
    .subscribe({
      next: (res:any)=>{
        console.log(res);
        this.getBookingDetailsPnr(pnr);
      }
    })
  }

  getBookingDetailsPnr(pnr:String) {
    this.router.navigate(['bookingDetails',pnr]);
    console.log('sent pnr:',pnr);
  }

}
