import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { SearchCriteria } from '../model/search-criteria.component';

@Injectable({
  providedIn: 'root'
})
export class FlightService {
  private Url = 'http://35.88.136.178:8081/api/v1.0/flight/airline/inventory';

  private Url1 = 'http://35.88.136.178:8081/api/v1.0/flight';


  host:string = "http://35.88.136.178:8081/api/v1.0/flight/flights";

  host1:string = "http://35.88.136.178:8081/api/v1.0/flight"

  constructor(private http: HttpClient) { }
  
  viewAllFlight() {
    return this.http.get(this.host);
  }

  addFlight(flight: Object): Observable<Object> {
    return this.http.post(`${this.Url}/add`, flight);
  }

  searchFlight(searchCriteria: SearchCriteria): Observable<Object> {
    console.log('search criteria found in service: ',searchCriteria);
    //return this.http.get(this.host1+"/search",searchCriteria);
    return this.http.post(`${this.Url1}/search`, searchCriteria);
  }


  
  
  viewFlight(flightNo: number): Observable<any> {
    return this.http.get(`${this.Url}/viewFlight/${flightNo}`);
  }

  

  

  modifyFlight(flightNo: number,value:any): Observable<Object> {
    return this.http.put(`${this.Url}/updateFlight`, value);
  }

  removeFlight(flightNo: number): Observable<any> {
    return this.http.delete(`${this.Url}/deleteFlight/${flightNo}`, { responseType: 'text' });
  }

  

}
