import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MyBookingService } from '../services/my-booking.service';
import { User } from '../model/user.component';
import { Booking } from '../model/booking.component';

@Component({
  selector: 'app-search-booking-pnr-cancel',
  templateUrl: './search-booking-pnr-cancel.component.html',
  styleUrls: ['./search-booking-pnr-cancel.component.css']
})
export class SearchBookingPnrCancelComponent implements OnInit {

  submitted = false;
  user: User=new User();
  booking: Booking = new Booking();
  pnr:String;

  constructor(private myBookingService: MyBookingService, private router: Router) { }

  ngOnInit(): void {
    //this.myBookingService=new MyBookingService();
  }

  cancelBooking(pnr:String){
    //this.submitted = true;
    this.myBookingService.cancelBooking(pnr)
    .subscribe({
      next: (res:any)=>{
        console.log(res);
        //this.getBookingDetailsPnr(pnr);
      }
    })
  }

  onSubmit() {
    this.submitted = true;
    this.cancelBooking(this.booking.pnr);
  }

  getBookingDetailsPnr(pnr:String) {
    this.router.navigate(['bookingDetails',pnr]);
    console.log('sent pnr:',pnr);
  }

}
