import { Component, OnInit } from '@angular/core';
import { Booking } from '../model/booking.component';
import { ActivatedRoute, Router } from '@angular/router';
import { MyBookingService } from '../services/my-booking.service';

@Component({
  selector: 'app-booking-details',
  templateUrl: './booking-details.component.html',
  styleUrls: ['./booking-details.component.css']
})
export class BookingDetailsComponent implements OnInit {

  bookingId: number;
  booking: Booking;
  pnr: String;

  constructor(private route: ActivatedRoute, private router: Router,
    private myBookingService: MyBookingService) { }

  ngOnInit() {
    this.booking = new Booking();


    this.pnr = this.route.snapshot.paramMap.get('pnr');
    console.log('got pnr:',this.pnr);

    this.myBookingService.getBookedDetails(this.pnr)
    .subscribe({
      next: (res:any)=>{
        console.log(res);
        this.booking = res;
      }
    })
  }
}
