import { Component, OnInit } from '@angular/core';
import { Booking } from '../model/booking.component';
import { Router } from '@angular/router';
import { BookingRequest } from '../model/booking-request';
import { User } from '../model/user.component';
import { MyBookingService } from '../services/my-booking.service';

@Component({
  selector: 'app-create-booking',
  templateUrl: './create-booking.component.html',
  styleUrls: ['./create-booking.component.css']
})
export class CreateBookingComponent implements OnInit {

  //bookingRequest: BookingRequest = new BookingRequest();
  bookingRequest: BookingRequest = {mealOpt: "", flightId: null,seatNumbers:[], user:null};
  user: User = new User();
  booking: Booking;
  submitted = false;
  flightId: number = 3;
  seatNumbers :Number[];
  constructor(private myBookingService: MyBookingService, private router: Router) { }

  ngOnInit(): void {
  }

  newBookingRequest(): void {
    this.submitted = false;
    this.bookingRequest = new BookingRequest();
  }

  save() {
    console.log('got bookingRequest from html: ',this.bookingRequest);
    console.log('got user from html: ',this.user);
    this.bookingRequest.user = this.user;
    this.bookingRequest.seatNumbers = this.seatNumbers;
    console.log('got bookingRequest2 from html: ',this.bookingRequest);
    this.myBookingService.bookTicket(this.bookingRequest)
    .subscribe((data:any) => {console.log(data);
    this.booking=data;},error => console.log(error));

    
    this.bookingRequest = new BookingRequest();
    //this.gotoList();
  }

  onSubmit() {
    this.submitted = true;
    this.save();
  }

  gotoList() {
    this.router.navigate(['/bookings']);
  }

}
