import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CreateBookingComponent } from './create-booking/create-booking.component';
import { BookingDetailsComponent } from './booking-details/booking-details.component';
import { FlightListComponent } from './flight-list/flight-list.component';
import { CreateFlightComponent } from './create-flight/create-flight.component';
import { WelcomeAdminComponent } from './welcome-admin/welcome-admin.component';
import { CreateAirlineComponent } from './create-airline/create-airline.component';
import { AirlineListComponent } from './airline-list/airline-list.component';
import { SearchBookingPnrComponent } from './search-booking-pnr/search-booking-pnr.component';
import { SearchBookingMailComponent } from './search-booking-mail/search-booking-mail.component';
import { BookingDetailsMailComponent } from './booking-details-mail/booking-details-mail.component'; 
import { SearchBookingPnrCancelComponent } from './search-booking-pnr-cancel/search-booking-pnr-cancel.component';
import { SearchBookingChoiceComponent } from './search-booking-choice/search-booking-choice.component';
import { SearchFlightComponent } from './search-flight/search-flight.component';
import { ChangeAirlineStatusComponent } from './change-airline-status/change-airline-status.component';

const routes: Routes = [
  {path: 'addBooking', component: CreateBookingComponent},
  {path:'', redirectTo:'/home', pathMatch:'full'},
  {path: 'bookingDetails/:pnr', component: BookingDetailsComponent},
  {path: 'home', component: WelcomeAdminComponent},
  {path: 'airlines', component: AirlineListComponent },
  {path: 'addAirline', component: CreateAirlineComponent },
  {path: 'flights', component: FlightListComponent },
  {path: 'addFlight', component: CreateFlightComponent },
  {path: 'welcomeAdmin', component:WelcomeAdminComponent},
  {path: 'searchBookingPnr', component: SearchBookingPnrComponent},
  {path: 'searchBookingMail',component: SearchBookingMailComponent},
  {path: 'bookingDetailsMail/:mail', component: BookingDetailsMailComponent},
  {path: 'searchBookingPnrCancel', component: SearchBookingPnrCancelComponent},
  {path: 'searchBookingChoice', component: SearchBookingChoiceComponent},
  {path: 'searchFlight', component: SearchFlightComponent},
  {path: 'changeAirlineStatus', component:ChangeAirlineStatusComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents= [
  WelcomeAdminComponent,
  SearchBookingPnrComponent
]
