import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MyBookingService } from '../services/my-booking.service';
import { User } from '../model/user.component';
import { Booking } from '../model/booking.component';

@Component({
  selector: 'app-search-booking-mail',
  templateUrl: './search-booking-mail.component.html',
  styleUrls: ['./search-booking-mail.component.css']
})
export class SearchBookingMailComponent implements OnInit {

  user: User=new User();
  booking: Booking = new Booking();
  mail:String;


  constructor(private myBookingService: MyBookingService, private router: Router) { }

  ngOnInit(): void {
    //this.myBookingService=new MyBookingService();
  }

  getBookingHistory(mail:String){
    this.myBookingService.getBookingHistory(mail)
    .subscribe({
      next: (res:any)=>{
        console.log(res);
        this.getBookingDetailsMail(mail);
      }
    })
  }

  getBookingDetailsMail(mail:String) {
    this.router.navigate(['bookingDetailsMail',mail]);
    console.log('sent mail:',mail);
  }

}
